﻿import { Component, ViewEncapsulation, ElementRef, Input, OnInit, OnDestroy } from '@angular/core';

import { JWModalService } from './jwmodal.service';

@Component({ 
    selector: 'jw-modal', 
    templateUrl: 'jwmodal.component.html', 
    styleUrls: ['jwmodal.component.less'],
    encapsulation: ViewEncapsulation.None
})
export class JWModalComponent implements OnInit, OnDestroy {
    @Input() id: string;
    @Input() number_modal: number;
    private element: any;
    estilo = {};
    estilobg = {};

    constructor(private modalService: JWModalService, private el: ElementRef) {
        this.element = el.nativeElement;
    }

    ngOnInit(): void {
        // ensure id attribute exists
        if (!this.id) {
            console.error('modal must have an id');
            return;
        }
     
        if (this.number_modal==2){
            this.estilobg = {'z-index': '2020'};
            this.estilo = {'z-index': '2021'};
        }
        
        if (this.number_modal==3){
            this.estilobg = {'z-index': '2030'};
            this.estilo = {'z-index': '2031'};
        }
        
        if (this.number_modal==4){
            this.estilobg = {'z-index': '2040'};
            this.estilo = {'z-index': '2041'};
        }
        

        // move element to bottom of page (just before </body>) so it can be displayed above everything else
        document.body.appendChild(this.element);

        // close modal on background click
        this.element.addEventListener('click', el => {
            if (el.target.className === 'jw-modal') {
                //impede fechar o modal ao clicar fora dele
                //this.close();
            }
        });

        
        // add self (this modal instance) to the modal service so it's accessible from controllers
        this.modalService.add(this);
    }

    // remove self from modal service when component is destroyed
    ngOnDestroy(): void {
        this.modalService.remove(this.id);
        this.element.remove();
    }

    // open modal
    open(): void {
        this.element.style.display = 'block';
        document.body.classList.add('jw-modal-open');
    }

    // close modal
    close(): void {
        this.element.style.display = 'none';
        document.body.classList.remove('jw-modal-open');
    }
}